
import React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowDown, faArrowUp } from '@fortawesome/free-solid-svg-icons';
import { Col, Row, Card, Button, Table } from '@themesberg/react-bootstrap';
import { Link } from 'react-router-dom';

import { pageVisits } from "../data/tables";
import transactions from "../data/transactions";


export const Top10Table = (props) => {
  const { TOP10GECKO } = props;
  
  const TableRow = (props) => {
    /* console.log(props); */
    let formatter = new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' });

    const { market_cap_rank, name, current_price, market_cap, price_change_percentage_24h } = props;
    const bounceIcon = price_change_percentage_24h < 0 ? faArrowDown : faArrowUp;
    const bounceTxtColor = price_change_percentage_24h < 0 ? "text-danger" : "text-success";

    return (
      <tr>
        <th scope="row">{market_cap_rank}</th>
        <td>{name}</td>
        <td>{formatter.format(current_price)}</td>
        <td>{formatter.format(market_cap)}</td>
        <td>
          <FontAwesomeIcon icon={bounceIcon} className={`${bounceTxtColor} me-3`} />
          {Math.abs(price_change_percentage_24h).toFixed(2)}%
        </td>
      </tr>
    );
  };

  return (
    <Card border="light" className="shadow-sm">
      <Card.Header>
        <Row className="align-items-center">
          <Col>
            <h5>Top 10</h5>
          </Col>
          {/* <Col className="text-end">
            <Button variant="secondary" size="sm">Atualizar</Button>
          </Col> */}
        </Row>
      </Card.Header>
      <Table responsive className="align-items-center table-flush">
        <thead className="thead-light">
          <tr>
            <th scope="col">Rank</th>
            <th scope="col">Nome</th>
            <th scope="col">Preo</th>
            <th scope="col">Capitalizao</th>
            <th scope="col">Variaao 24h</th>
          </tr>
        </thead>
        <tbody>
          { TOP10GECKO.map(coin => <TableRow key={`${coin.id}`} {...coin} />) }
        </tbody>
      </Table>
    </Card>
  );
};


export const TransactionsTable = (props) => {
  const { dataTable } = props;
  const totalTransactions = transactions.length;

  const TableRow = (props) => {
    const { invoiceNumber, subscription, price, issueDate, dueDate, status } = props;
    const statusVariant = status === "Paid" ? "success"
      : status === "Due" ? "warning"
        : status === "Canceled" ? "danger" : "primary";

    return (
      <tr>
        <td>
          <Card.Link as={Link} to="{Routes.Invoice.path}" className="fw-normal">
            {invoiceNumber}
          </Card.Link>
        </td>
        <td>
          <span className="fw-normal">
            {subscription}
          </span>
        </td>
        <td>
          <span className="fw-normal">
            {issueDate}
          </span>
        </td>
        <td>
          <span className="fw-normal">
            {dueDate}
          </span>
        </td>
        <td>
          <span className="fw-normal">
            {parseFloat(price).toFixed(2)}
          </span>
        </td>
        <td>
          <span className={`fw-normal text-${statusVariant}`}>
            {status}
          </span>
        </td>
      </tr>
    );
  };

  return (
    <Card border="light" className="table-wrapper table-responsive shadow-sm">
      <Card.Body className="pt-0">
        <Table hover className="user-table align-items-center">
          <thead>
            <tr>
              <th className="border-bottom">Rank</th>
              <th className="border-bottom">Nome</th>
              <th className="border-bottom">Cotao</th>
              <th className="border-bottom">Alta</th>
              <th className="border-bottom">Baixa</th>
              <th className="border-bottom">Volume</th>
            </tr>
          </thead>
          <tbody>
            {dataTable !== undefined ? dataTable.map(t => <TableRow key={`transaction-${t.invoiceNumber}`} {...t} />) : transactions.map(t => <TableRow key={`transaction-${t.invoiceNumber}`} {...t} />)}
          </tbody>
        </Table>
      </Card.Body>
    </Card>
  );
};
